.user-msg {
    z-index: 10;
    color: $fontColor;
    padding: 10px;
    font-size: rem(17px);
    position: fixed;
    top: 30px;
    left: 20px;
    background-color: $bgcColor;
    .txt-msg svg {
        margin-right: 30px;
    }
    button {
        background-color: $bgcColor;
        border: 2px solid black;
        border-radius: 50%;
        margin: 10px;
        color: $fontColor;
        &:hover {
            background-color: $clr11;
        }
    }

    &.success {
        border: 8px solid rgb(41, 189, 73);
    }
    &.danger {
        border: 8px solid rgb(216, 59, 59);
    }
}
