.main-container {
    display: grid;
    place-content: center;
    column-gap: 1rem;

    grid-template-columns: 0px 1fr 0px;
    grid-template-columns: 0px minmax(auto, 450px) 0px;

    > * {
        grid-column: 2;
    }

    > .full {
        grid-column: 1 / -1;
    }

